// scrollReveal
export default function retScroll (data, state, webPercentage, javaPercentage, linuxPercentage) {
  // reveal()id (#reveal-top) class(.reveal-top)  class 
  data.scrollReveal.reveal('#reveal-heard', {
    // 
    duration: 500,
    // 
    // delay: 150,
    // 'bottom', 'left', 'top', 'right'
    origin: 'bottom',
    // 
    reset: false,
    // alwaysonceonload
    useDelay: 'onload',
    // 
    mobile: true,
    // %rem
    // distance: '20px',
    // 
    //  
    // easing: 'cubic-bezier(0.5, 0, 0, 1)',
    // 
    scale: 0.6
    // beforeReveal: function (ele) {
    //   // 
    //   state.header = 1
    // }
  })
  // 
  data.scrollReveal.reveal('#reveal-introduce', {
    // 
    duration: 500,
    // 
    // delay: 150,
    // 'bottom', 'left', 'top', 'right'
    origin: 'top',
    // 
    reset: false,
    // alwaysonceonload
    useDelay: 'onload',
    // 
    mobile: true,
    // %rem
    distance: '-30px',
    // 
    //  
    easing: 'cubic-bezier(0.5, 0, 0, 1)',
    // 
    scale: 0.8
    // beforeReveal: function (ele) {
    //   // 
    //   state.introduce = 1
    // }
  })
  // 
  data.scrollReveal.reveal('#reveal-experience', {
    // 
    duration: 300,
    // 
    delay: 150,
    // 'bottom', 'left', 'top', 'right'
    // origin: 'right',
    // 
    reset: false,
    // alwaysonceonload
    useDelay: 'onload',
    // 
    mobile: true,
    // %rem
    // distance: '-30px',
    // 
    //  
    easing: 'cubic-bezier(0.5, 0, 0, 1)',
    // 
    scale: 0.8,
    // beforeReveal: function (ele) {
    //   // 
    //   state.experience = 1
    // },
    afterReveal: function (ele) {
      if (webPercentage.value) {
        webPercentage.value.start()
      }
      if (javaPercentage.value) {
        javaPercentage.value.start()
      }
      if (linuxPercentage.value) {
        linuxPercentage.value.start()
      }
    }
  })
  // 
  data.scrollReveal.reveal('#reveal-project', {
    // 
    duration: 500,
    // 
    delay: 150,
    // 'bottom', 'left', 'top', 'right'
    // origin: 'top',
    // 
    reset: false,
    // alwaysonceonload
    useDelay: 'onload',
    // 
    mobile: true,
    // %rem
    // distance: '-30px',
    // 
    //  
    easing: 'cubic-bezier(0.5, 0, 0, 1)',
    // 
    scale: 0.8
    // beforeReveal: function (ele) {
    //   // 
    //   state.project = 1
    // }
  })
}
